body {
  font: 60px "Century Gothic", Futura, sans-serif;
  
  background-color: #ccc33;
  background-image: url("https://images.unsplash.com/photo-1498612753354-772a30629934?ixlib=rb-1.2.1&auto=format&fit=crop&w=634&q=80");
  //background-size: cover;
  //margin: 20px;
  width: 50%;
  margin:auto;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
  margin: auto;
  width: 50%;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 20vh;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 20vh;
  //background-image: url("https://peterjochem.github.io/corkboard/The_GAN_Zoo.jpg");
  //background-size: cover;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

